@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap");
:root {
  --font-body: "Outfit", sans-serif;
  --font-medium: 18px;
}

:root {
  --container-bg: hsl(217, 54%, 11%);
  --card-bg: hsl(216, 50%, 16%);
  --line: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
  --link-hover: hsl(178, 100%, 50%);
  --color-text: hsl(215, 51%, 70%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  font-family: var(--font-body);
}

img {
  max-width: 100%;
  display: block;
}

.container {
  min-height: 100vh;
  background-color: var(--container-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
}
@media (min-width: 40em) {
  .container {
    padding: 0;
  }
}

.card__link {
  text-decoration: none;
  color: var(--white);
  transition: all ease 0.5s;
}

.card__link:hover {
  color: var(--link-hover);
}

.card {
  background-color: var(--card-bg);
  padding: 1.25rem;
  border-radius: 1rem;
  max-width: 22.5rem;
}
@media (min-width: 40em) {
  .card {
    padding: 1.5625rem;
  }
}
.card__image {
  position: relative;
  border-radius: 0.625rem;
  overflow: hidden;
}
.card__image::before {
  position: absolute;
  background-color: rgba(0, 255, 247, 0.6);
  content: url(../images/icon-view.svg);
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  max-height: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  transition: all ease 0.5s;
}
.card__image:hover::before {
  max-height: 100%;
}
.card__title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-top: 1rem;
}
.card__description {
  color: var(--color-text);
  font-size: var(--font-medium);
  font-weight: 300;
  line-height: 1.5;
  margin: 1rem 0;
}
.card__cryptocurrency {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 0.9375rem;
  border-bottom: 1px solid var(--line);
}
.card__cryptocurrency--div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4375rem;
}
.card__cryptocurrency--div .card__eth {
  color: var(--link-hover);
  font-weight: 600;
}
.card__cryptocurrency--div .card__time {
  color: var(--color-text);
}
.card__profile {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.625rem;
  margin-top: 1rem;
}
.card__profile .card__avatar {
  width: 1.875rem;
  border-radius: 50%;
  border: 1px solid var(--white);
}
.card__profile .card__subtitle {
  color: var(--color-text);
  font-size: 1rem;
  font-weight: normal;
}